import { useState } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { Redirect } from 'react-router-dom';

import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { SET_UPDATE_USER, CANCEL_UPDATE_USER, updateUser } from '../../services/actions/auth';
import { logout } from '../../services/actions/auth';


import styles from './profile-form.module.css';

export const ProfileForm = () => {

  const dispatch = useDispatch();
  const form = useSelector(store => store.user.form);
  const [actionButtons, setActionButtons] = useState(false);
  const { isAuth } = useSelector(store => store.user);

  const onChange = (evt) => {
    dispatch({
      type: SET_UPDATE_USER,
      payload: {...form, [evt.target.name]: evt.target.value}
    });
    setActionButtons(true)
  };

  const onSubmitForm = (evt) => {
    evt.preventDefault();
    dispatch(updateUser(form));
    setActionButtons(false);
  }; 

  const cancelUpdateForm = () => {
    dispatch({ 
      type: CANCEL_UPDATE_USER 
    });
  };

  if (!isAuth) {
    return (
      <Redirect to={{ pathname: '/login' }} />
    );
  }  

  return (
    <form onSubmit={onSubmitForm} className={`${styles.form} mb-20`}>
          <fieldset className={styles.fieldset}>
            <Input
              type={'text'}
              placeholder={''}
              onChange={onChange}
              value={form.name}
              name={'name'}
              icon={'EditIcon'}
              errorText={""}          
            />
            <Input
              type={'email'}
              placeholder={''}
              onChange={onChange}
              value={form.email}
              name={'email'}
              icon={'EditIcon'}
              errorText={""}
            />
            <Input
              type={'password'}
              placeholder={''}
              onChange={onChange}
              value={form.password || ''}
              name={'password'}
              icon={'EditIcon'}
              errorText={""}
            />
          </fieldset>

          {actionButtons && (<div className={styles.actions}>
            <Button onClick={cancelUpdateForm} type="secondary" size="medium">
              
            </Button>

            <Button type="primary" size="large">
              
            </Button>
          </div>)}

        </form>

  )
}