import orderDetailsStyles from "./order-details.module.css";
import orderDoneImg from "../../images/order-done.png";
import { useSelector } from "react-redux";

const OrderDetails = () => {
  const orderNumber = useSelector((store) => store.order.order);

  return (
    <div className={`${orderDetailsStyles.container} pt-15`}>
      <h1 className="text text_type_digits-large mb-10">{orderNumber}</h1>
      <h2 className="text text_type_main-medium mb-15"> </h2>
      <img src={orderDoneImg} alt="  " />
      <span className="text text_type_main-default mt-15 mb-3">
           
      </span>
      <span className="text text_type_main-default text_color_inactive mb-30">
            
      </span>
    </div>
  );
};

export default OrderDetails;
